@import url(https://fonts.googleapis.com/css?family=Merienda:400,700);
body {
  margin: 0;
  font-family: Merienda, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.App {
  text-align: center;
}

.MultiTesting {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.MultiForm {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    border: 1px solid green;
    border-radius: 10px;
    padding: 20px;
    margin: 30px 0;
    box-shadow: 0 0 20px lightgreen;
}

.MultiForm select, .MultiForm input, .MultiForm textarea {
    font-family: Merienda, Arial, Helvetica, sans-serif;
    font-size: 1.0em;
    margin: 3px 0;
}

.MultiForm input {
    width: 300px;
}

.MultiForm textarea {
    width: 300px;
    max-width: 300px;
    min-width: 200px;
    min-height: 30px;
}

.MultiForm button {
    background: -webkit-radial-gradient(center, ellipse, rgb(56, 194, 38) 0%,#52a5e5 100%);
    background: radial-gradient(ellipse at center, rgb(56, 194, 38) 0%,#52a5e5 100%);
    color: white;
    font-weight: bold;
    border: 1px solid green;
    border-radius: 20px;
    cursor: pointer;
    padding: 8px 25px;
    outline: none !important;
    text-shadow: 1px 1px 1px black;
    font-size: 1.1em;
    font-family: Merienda;
    width: 170px;
}

.MultiForm button:hover {
    background: -webkit-radial-gradient(center, ellipse, #52a5e5 0%,rgb(56, 194, 38) 100%);
    background: radial-gradient(ellipse at center, #52a5e5 0%,rgb(56, 194, 38) 100%);
}

@-webkit-keyframes border {
    0% {
        border-color: darkorange;
    }
    50% {
        border-color: #c9e432; 
    }
    100% {
        border-color: darkorange;
    }
}

@keyframes border {
    0% {
        border-color: darkorange;
    }
    50% {
        border-color: #c9e432; 
    }
    100% {
        border-color: darkorange;
    }
}
.MultiForm input:focus, .MultiForm textarea:focus, .MultiForm select:focus {
    border-color: #c9e432;
    -webkit-animation-name: border;
            animation-name: border;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    animation-direction: alternate-reverse;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    outline: none;
}

.MultiTable {
    text-align: left;
    border-collapse: collapse;
    box-shadow: 0 0 30px lightgoldenrodyellow;
}

.MultiTable th, .MultiTable td {
    border: 1px solid green;
    font-size: 0.9em;
    padding: 3px;
}

.MultiTable thead {
    background-color: lightgoldenrodyellow;
}

.MultiTable td {
    font-size: 0.8em;
}

.MultiTable tbody tr:nth-child(even) {
    background-color: lightgray;
}

.MultiTable .lowbase {
    color: red;
}

tr td.red {
    background-color: red;
}

tr td.green {
    background-color: green;
}

tr td {
    cursor: cell;
}

