.MultiTable {
    text-align: left;
    border-collapse: collapse;
    box-shadow: 0 0 30px lightgoldenrodyellow;
}

.MultiTable th, .MultiTable td {
    border: 1px solid green;
    font-size: 0.9em;
    padding: 3px;
}

.MultiTable thead {
    background-color: lightgoldenrodyellow;
}

.MultiTable td {
    font-size: 0.8em;
}

.MultiTable tbody tr:nth-child(even) {
    background-color: lightgray;
}

.MultiTable .lowbase {
    color: red;
}
